import { ApplicationModel } from '@app/application.model';
import { Injectable } from '@angular/core';
import { select } from '@ngrx/store';
import { Observable } from 'rxjs';

import { LoadProductsAction } from '@store/actions/models/products/products.action';
import { ProductsInterface } from '@core/interfaces/products.interface';


@Injectable()
export class HomeModel extends ApplicationModel {

    public isLoading$: Observable<boolean> = this.store.pipe(
        select(store => store.models.products.loading)
    );

    public isErrorProduct$: Observable<boolean> = this.store.pipe(
      select(store => store.models.products.error)
    );

    public products$: Observable<ProductsInterface> = this.store.pipe(
      select(store => store.models.products.types_account)
    );

    public fetchProducts(): void {
        this.store.dispatch(new LoadProductsAction());
    }
}
