import { Component, Input, ViewEncapsulation } from '@angular/core';

import { Product } from '@core/models/products/product';
import { isNullOrUndefined } from 'util';
import { CREDIT_CARD, DEPOSIT_ACCOUNT, CERTIFIED_DEPOSIT_TERM, CURRENT_ACCOUNT } from '@core/constants/types_account';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class ProductsComponent {

  @Input() type_account: string;
  @Input() products: Product[];

  public DEPOSIT: string = DEPOSIT_ACCOUNT;
  public CC: string = CREDIT_CARD;
  public CURRENT: string = CURRENT_ACCOUNT;
  public CDT: string = CERTIFIED_DEPOSIT_TERM;

  constructor() { }

  get hasProducts(): boolean {
    return !isNullOrUndefined(this.products) && this.products.length > 0;
  }

  get quantityProducts(): number {
    return !isNullOrUndefined(this.products) ? this.products.length : 0;
  }

}
