import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { environment as ENV } from '@environment';
import { RespondServiceProducts } from '@core/interfaces/products.interface';

@Injectable()
export class ProductsService {

  constructor(private http: HttpClient) { }

  public allProducts(): Observable<RespondServiceProducts> {

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json'
      })
    };

    const user = {
      'id': '19800203',
      'idType': 'CC',
      'requestId': '123234234',
      'ipAddress': '192.168.12.1',
      'companyId': 'BANCO_POPULAR',
      'currentSystemDate': ''
    };
    
      return this.http.post<RespondServiceProducts>(ENV.api.base + ENV.api.services.products, user, httpOptions);
  }
}
