import { catchError, map, switchMap } from 'rxjs/operators';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Injectable } from '@angular/core';
import { Action } from '@ngrx/store';
import { Observable, of } from 'rxjs';

import { ProductsService } from '@modules/home/services/products/products.service';
import * as productsActions from '@store/actions/models/products/products.action';
import { RespondServiceProducts } from '@core/interfaces/products.interface';

@Injectable()
export class ProductsEffects {

  constructor(
    private actions$: Actions,
    private productsService: ProductsService
  ) { }

  @Effect()
    LoadProducts: Observable<Action> = this.actions$.pipe(
      ofType<productsActions.LoadProductsAction>(productsActions.LOAD_PRODUCTS),
      switchMap((action: productsActions.LoadProductsAction) => {
        return this.productsService.allProducts()
          .pipe(
            map((items: RespondServiceProducts) => new productsActions.LoadProductsSuccessAction(items.products)),
            catchError(_ => of(new productsActions.LoadProductsFailAction()))
          );
      })
    );
}
