import { AfterViewInit, Component, Input, ViewEncapsulation } from '@angular/core';
import { tns } from '../../../../node_modules/tiny-slider/src/tiny-slider';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class SliderComponent implements AfterViewInit {

  @Input() id: string = `slider-${Math.floor(Math.random() * 1000) + 1}`;
  @Input() className: string;
  @Input() slides: Array<any>;
  @Input() controls: boolean = true;
  @Input() items: number = 1;
  @Input() nav: boolean = true;
  @Input() autoPlay: boolean = false;
  @Input() showAutoPlayButton: boolean = false;

  constructor() {
  }

  ngAfterViewInit(): void {
    const slider = tns({
      container: `.slider-${this.id}`,
      items: this.items,
      slideBy: 'page',
      loop: true,
      autoplay: this.autoPlay,
      mouseDrag: true,
      controls: this.controls,
      nav: this.nav,
      autoplayButtonOutput: this.showAutoPlayButton,
      controlsText: ['', '']
    });
  }

}
