import { Component, Input, ViewEncapsulation } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { Router } from '@angular/router';

import { ManipulateDomService } from '@core/services/manipulate-dom/manipulate-dom.service';
import { Product } from '@core/models/products/product';
import { validateSizeNumber } from '@app/shared/helpers/numbers.helper';

@Component({
  selector: 'app-product-credit-card',
  templateUrl: './product-credit-card.component.html',
  styleUrls: ['./product-credit-card.component.sass'],
  encapsulation: ViewEncapsulation.None
})
export class ProductCreditCardComponent {

  @Input() product: Product;

  constructor(
    private router: Router,
    private dom: ManipulateDomService
  ) { }

  get hasProduct(): boolean {
    return !isNullOrUndefined(this.product);
  }

  public redirect(type: string, id: string): void {
    this.router.navigate(['/detalle/', type.toLowerCase(), id]);
    this.dom.scrollTop();
  }

  public validateSize(amount: number): boolean {
    return validateSizeNumber(amount, 999000000);
  }

}
