import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { isNullOrUndefined } from 'util';
import { filter, map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { ProductsInterface } from '@core/interfaces/products.interface';
import { Product } from '@core/models/products/product';
import { HomeModel } from '@modules/home/home.model';

@Component({
  selector: 'app-home',
  templateUrl: './home.container.html',
  styleUrls: ['./home.container.sass'],
  encapsulation: ViewEncapsulation.None
})
export class HomeContainer implements OnInit {

  public loadingProducts: Array<string> = Array(3).fill([]);
  public offersProducts: Array<object> = [
    {
      img: '/offer-products/cuenta-express.png',
      name: 'Cuenta exprés'
    },
    {
      img: '/offer-products/cuenta-protegida.png',
      name: 'Cuenta protegida'
    },
    {
      img: '/offer-products/tarjeta-de-credito.png',
      name: 'Tarjeta de crédito'
    },
    {
      img: '/offer-products/compra-de-cartera.png',
      name: 'Compra de cartera'
    }
  ];

  constructor(
      private model: HomeModel
  ) { }

  ngOnInit(): void {
    this.model.fetchProducts();
  }

  get hasProducts$(): Observable<boolean> {
    return this.products$
        .pipe(
          filter(data => !isNullOrUndefined(data)),
          map( (products: ProductsInterface) => this.joinArrays(products).length > 0)
        );
  }

  get isLoadingProducts$(): Observable<boolean> {
    return this.model.isLoading$;
  }

  get isErrorProducts$(): Observable<boolean> {
    return this.model.isErrorProduct$;
  }

  get products$(): Observable<ProductsInterface> {
    return this.model.products$;
  }

  private joinArrays(products: ProductsInterface): Array<Product> {

    const allProducts: Array<Product> = [];

    if (!isNullOrUndefined(products)) {
      for (const product in products) {
        if (products.hasOwnProperty(product))
          allProducts.push.apply(allProducts, products[product]);
      }
    }

    return allProducts;
  }

}
